.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* ======= My CSS ======= */
html {
    background-image: url('assets/images/main-bg.jpg') !important;
    background-size: cover;
    background-repeat: no-repeat;
}

#main-div {
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    background-color: rgb(31, 30, 30);
    color: whitesmoke;
    border-radius: 10px;
    align-self: center;
    justify-content: center;
    margin: 25% auto;
    width: 50%;
    padding: 10px;
    box-shadow: 0 0 .2rem #fff,
            0 0 .2rem #fff,
            0 0 2rem #bc13fe,
            0 0 0.8rem #bc13fe,
            0 0 2.8rem #bc13fe,
            inset 0 0 1.3rem #bc13fe;

    animation: pulsebox 1.5s infinite alternate;
}

#main-div button {
    background-color: rgb(40, 38, 39);
    color: whitesmoke;
    border: 1px solid black;
    border-radius: 2px;
    padding: 5px 5px;
    align-self: center;
    justify-self: center;
}

#main-div button:hover {
    cursor: pointer !important;
    background-color: rgb(50, 48, 49) !important;
}

#main-div hr {
    box-shadow: 0 0 .2rem #fff,
            0 0 .2rem #8c0cbf,
            0 0 2rem #bc13fe,
            0 0 1.8rem #bc13fe,
            0 0 2.8rem #bc13fe,
            inset 0 0 1.3rem #bc13fe;
}

#result-div {
    --gap: 2em;
    --line-offset: calc(var(--gap) / 2);
    --line-thickness: 2px;
    --line-color: whitesmoke;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    overflow: hidden;
    gap: var(--gap);
}

.result-display-format {
    position: relative;
    color: whitesmoke;
    text-align: center;
    align-self: center;
    justify-content: center;
    padding: 5px;
}

.result-display-format > h4 {
    font-size: 16pt;
    font-weight: 440;
}

.result-display-format > div {
    font-size: 22pt;
    font-weight: 330;
}

.result-display-format::before, .result-display-format::after {
    content: "";
    position: absolute;
    background-color: var(--line-color);
    z-index: 1;
}

.result-display-format::after {
    inline-size: 100vw;
    block-size: var(--line-thickness);
    inset-inline-start: 0;
    inset-block-start: calc(var(--line-offset) * -1);
}

.result-display-format::before {
    inline-size: var(--line-thickness);
    block-size: 100vh;
    inset-block-start: 0;
    inset-inline-start: calc(var(--line-offset) * -1);
}
  

#buttons-display {
    display: inline-flex;
    position: relative;
    align-self: center;
    justify-content: center;
}

button:disabled, button[disabled] {
  background-color: rgba(35, 33, 34, 0.338);
}

@keyframes pulsebox {
    100% {
        box-shadow: 0 0 .2rem #fff,
            0 0 .2rem #fff,
            0 0 2rem #bc13fe,
            0 0 0.8rem #bc13fe,
            0 0 2.8rem #bc13fe,
            inset 0 0 1.3rem #bc13fe;
    }

    0% {
        box-shadow: 0 0 .2rem #fff,
            0 0 .2rem #fff,
            0 0 2rem #bc13fe,
            0 0 0.4rem #bc13fe,
            0 0 0.7rem #bc13fe,
            inset 0 0 0.5rem #bc13fe;
    }
}

#indicator-message-display {
  display: block;
  min-width: fit-content;
  margin: auto;
  font-size: 12pt;
}

@media screen and (max-width: 480px) {
  #main-div {
    width: 80vw;
  }

  .result-display-format > h4 {
    font-size: 14pt;
    font-weight: 440;
  }

  .result-display-format > div {
    font-size: 18pt;
    font-weight: 330;
  }
}